@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@300;400;700&display=swap);
* {
  font-family: 'Source Sans Pro', sans-serif;
}
body {
  margin: 0;
  padding: 0;
}
section  > h1 {
  margin: 1rem 0;
  font-weight: bold;
}
section > div{
  margin-bottom: 2rem;
}
section > div  h4 {
  margin-bottom: 1rem;
}
.header {
  width: 100%;
  flex-shrink: 0;
  flex-grow: 0;
  height: 5.1429rem;
  background-color: #ffffff;
  display: flex;
  align-items: center;
  padding: 1rem 1.5rem;
  justify-content: space-between;
  border-bottom: 1px solid #dee2eb;
}
.sidebar {
  width: 25%;
  border-right: 1px solid var(--gray3);
  position: fixed;
  height: 100%;
  overflow: auto;
}
.sidebar .logo {
  padding: 1rem;
  margin-bottom: 1rem;
  text-align: center;
  border-bottom: 1px solid var(--gray3);
}
.sidebar .logo img {
  max-width: 150px;
  display: inline-block;
}
.sidebar ul li {
  font-weight: normal;
}
.sidebar ul li.li-header,
.sidebar ul li.li-subheader {
  padding: .5rem 1rem;
  font-weight: bold;
}
.sidebar ul li a {
  color: var(--text-dark);
  text-decoration: none;
  display: block;
  padding: .5rem 1.5rem;
}
.sidebar ul li a:hover {
  background-color: var(--gray1);
}
.content {
  flex-grow: 1;
  overflow-y: auto;
  padding: 1rem 2rem;
  margin-left: 25%;
  width: 70%;
}
.example {
  border: 5px solid var(--gray1);
  margin-bottom: 3rem;
}
.example .design {
  padding: 3rem;
}
.example pre {
  padding: 3rem;
  background-color: var(--gray1) !important;
  margin: 0 !important;
  border-radius: 0 !important;
}
.example pre code {
  background-color: transparent !important;
}
